* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
}

.link-item {
  color: #0188ff;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  transition: all 1.5s;
  display: flex;
}

.main-list__item {
  width: 180px;
  height: 50px;
  background-color: #000;
  border: 1px solid #005aa7;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  transition: all 1.5s;
  display: flex;
}

.main-list__item:hover {
  animation-name: backgroundAnimation;
  animation-duration: 1.5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.main-list__item:hover a {
  color: #000;
  transition: all 1.5s;
}

a {
  text-decoration: none;
}

.main-list {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

@keyframes backgroundAnimation {
  0% {
    background: linear-gradient(to right, #36d1dc, #5b86e5);
  }

  25% {
    background: linear-gradient(to right, #36d1dc, #36d1dc, #5b86e5, #36d1dc);
  }

  50% {
    background: linear-gradient(to right, #36d1dc, #5b86e5, #36d1dc);
  }

  75% {
    background: linear-gradient(to left, #36d1dc, #36d1dc, #5b86e5, #36d1dc);
  }

  100% {
    background: linear-gradient(to left, #36d1dc, #5b86e5);
  }
}

